<template>
  <div>
    <form class="form">
      <input type="text" v-model="newUser.name" placeholder="–ò–º—è" />
      <input type="email" v-model="newUser.email" placeholder="Email" />
      <span v-if="emailError" style="color: red">{{ emailError }}</span>
      <select v-model="newUser.roleId">
        <option disabled value="">–í—ã–±–µ—Ä–∏—Ç–µ —Ä–æ–ª—å</option>
        <option v-for="role in roles" :key="role.id" :value="role.id">
          {{ role.name }}
        </option>
      </select>
      <button class="add-bt" @click="submitForm">–î–æ–±–∞–≤–∏—Ç—å</button>
    </form>

    <table>
      <thead>
        <tr>
          <th>–ò–º—è</th>
          <th>Email</th>
          <th>–†–æ–ª—å</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in users" :key="user.id">
          <td>{{ user.name }}</td>
          <td>{{ user.email }}</td>
          <td>{{ user.role }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      users: [],
      roles: [],
      newUser: {
        name: '',
        email: '',
        roleId: ''
      }
    }
  },
  methods: {
    validateEmail () {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
      if (!this.newUser.email) {
        this.emailError = '–ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ'
        return false
      } else if (!emailPattern.test(this.newUser.email)) {
        this.emailError = '–ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π email'
        return false
      } else {
        this.emailError = ''
        return true
      }
    },
    async submitForm () {
      if (!this.newUser.email || !this.newUser.name || !this.newUser.roleId) {
        alert('–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è!')
        return
      }

      if (!this.validateEmail()) {
        return
      }

      const response = await fetch('http://localhost:3000/users', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(this.newUser)
      })
      const result = await response.json()
      const role = this.roles.find(r => r.id === result.user.roleId)

      // –î–æ–±–∞–≤–ª—è–µ—Ç—Å—è –Ω–æ–≤—ã–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —Å –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º–æ–π —Ä–æ–ª—å—é
      this.users.push({
        ...result.user,
        role: role ? role.name : '–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–æ'
      })

      // –°–±—Ä–æ—Å —Ñ–æ—Ä–º—ã
      this.newUser = { name: '', email: '', roleId: '' }
    }
  },
  async mounted () {
    try {
      const [usersRes, rolesRes] = await Promise.all([ // –∂–¥—ë—Ç, –ø–æ–∫–∞ –æ–±–∞ –∑–∞–ø—Ä–æ—Å–∞ –∑–∞–≤–µ—Ä—à–∞—Ç—Å—è, –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–≤ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤ –≤ –≤–∏–¥–µ [usersRes, rolesRes]
        fetch('http://localhost:3000/users'), // –∑–∞–ø—Ä–∞—à–∏–≤–∞–µ—Ç —Å–ø–∏—Å–æ–∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π
        fetch('http://localhost:3000/roles') // –∑–∞–ø—Ä–∞—à–∏–≤–∞–µ—Ç —Å–ø–∏—Å–æ–∫ —Ä–æ–ª–µ–π
      ])

      const usersData = await usersRes.json() // –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ—Ç –æ—Ç–≤–µ—Ç usersRes –∏–∑ —Ñ–æ—Ä–º–∞—Ç–∞ JSON –≤ –æ–±—ä–µ–∫—Ç JavaScript
      const rolesData = await rolesRes.json()

      /* –°–æ—Ö—Ä–∞–Ω—è–µ—Ç –ø–æ–ª—É—á–µ–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –≤ —Å–≤–æ–π—Å—Ç–≤–∞ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ users –∏ roles, —á—Ç–æ–±—ã –ø–æ—Ç–æ–º –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –∏—Ö –≤ —à–∞–±–ª–æ–Ω–µ (v-for, v-model, –∏ —Ç.–ø.). */
      this.users = usersData
      this.roles = rolesData
    } catch (error) {
      console.error('Error fetching data', error)
    }
  }
}
</script>

<style lang="scss">
.form{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 24px;

  input,
  select{
    border: 1px solid #c4c4c4;
    padding: 4px 8px;
    width: 270px;
  }

  .add-bt{
    background-color: #00ac00;
    color: #fff;
    padding: 4px 12px;
  }
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);

  thead {
    background-color: #242424;
    color: #fff;

    th {
      padding: 12px;
      text-align: left;
      font-weight: bold;
    }
  }

  tbody {
    tr {
      &:nth-child(even) {
        background-color: #f9f9f9;
      }

      &:hover {
        background-color: #ececec;
      }
    }

    td {
      padding: 12px;
      border-top: 1px solid #ddd;
      color: #333;
    }
  }
}
</style>
